.section {
  &__text {
    p, h4, li {
      color: #E5E5E5;
      margin: 0;
    }

    h4 {
      font-family: 'Kumbh Sans', sans-serif;
      font-size: 24px;
      line-height: 28px;
      font-weight: 700;
    }

    p, li {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
    }

    h4 + ul {
      margin-top: 30px;
    }

    h4 + p {
      margin-top: 30px;
    }

    p + h4 {
      margin-top: 53px;
    }

    p + ul {
      margin-top: 30px;
    }

    p + p {
      margin-top: 30px;
    }

    ul + p {
      margin-top: 30px;
    }

    ul + h4 {
      margin-top: 53px;
    }

    ul + ul {
      margin-top: 30px;
    }

    ul {
      margin-left: 20px;

      li {
        list-style: disc;
      }
    }
  }

  &__wrapper {
    padding: 107px 0 116px 0;

    &-big {
      padding: 111px 0 120px 0;
    }

    &-medium {
      padding: 107px 0 104px 0;
    }

    &-small {
      padding: 101px 0 87px 0;
    }

    &-page {
      padding: 71px 0 115px 0;
    }

    &-why {
      padding: 116px 0 93px 0;
    }

    &-news {
      padding: 93px 0 103px 0;
    }

    &-faq-form {
      padding: 63px 0 58px 0;
    }

    &-faq {
      padding: 53px 0 131px 0;
    }

    &-contact {
      padding: 93px 0 125px 0;
    }
  }

  &__content {
    margin: 50px 0 0 0;
  }

  &__info {
    &-mobile {
      display: none;
    }
  }

  &-grey-black {
    background: #121212;
  }

  &-black {
    background-color: #0B0B0B;
  }

  &-bordered {
    border-top: 1px solid #302F2F;
    border-bottom: 1px solid #302F2F;

    &-top {
      border-bottom: none;
    }
  }

  &-video {
    &__grid {
      display: grid;
      grid-template-columns: auto 768px;
      gap: 51px;
    }
  }

  &-why {
    &__grid {
      display: grid;
      grid-template-columns: 526px auto;
      gap: 60px;
    }

    &__video {
      width: 100%;
      height: 500px;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        z-index: 2;
        width: 40px;
        height: 100%;
        display: block;
        right: 0;
        top: 0;

        background: url("../img/corner-right.svg") center center/cover no-repeat;
      }

      &-item {
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;

        object-fit: cover;
      }
    }
  }

  &-advantages {
    &__wrapper {
      background-color: #0B0B0B;
      position: relative;

      border-top: 1px solid #302F2F;
      border-bottom: 1px solid #302F2F;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;

        z-index: 0;

        width: 100%;
        height: 100%;
        display: block;

        background: url("../img/map.svg") top center/cover no-repeat;
      }
    }

    &__content {
      margin-top: 101px;
    }

    &__footer {
      margin-top: 41px;
    }

    &__container {
      position: relative;
      z-index: 3;
    }
  }

  &-plan {
    &__content {
      margin: 63px 0 0 0;
    }
  }

  &-zone {
    &__wrapper {
      display: grid;
      grid-template-columns: 671px auto;
      gap: 80px;

      &-reverse {
        grid-template-columns: auto 671px;
      }
    }

    &__content {
      margin-top: 47px;
    }

  }

  &-buy {
    &__wrapper {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
    }

    &__info {
      max-width: 280px;
      width: 100%;
    }

    &__content {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 396px));
      grid-auto-rows: 498px;

      gap: 38px;
    }
  }

  &-calculate {
    &__title {
      max-width: 443px;
    }

    &__wrapper {
      display: grid;
      grid-template-columns: auto 600px;
    }
  }

  &-banner {
    &__wrapper {
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;

      &-plan {
        background-image: url("../img/banner-plan.jpg");
      }

      &-difference {
        background-image: url("../img/banner-difference.jpg");
      }

      &-news {
        background-image: url("../img/banner-news.jpg");
      }

      &-partner {
        background-image: url("../img/banner-partner.jpg");
      }

      &-ask {
        background-image: url("../img/banner-ask.jpg");
      }

      &-download {
        background-image: url("../img/banner-download.jpg");
      }

      &-contact {
        background-image: url("../img/banner-contact.jpg");
      }

      &-zone {
        background-image: url("../img/banner-zone.jpg");
      }

      &-settings{
        background-image: url("../img/banner-settings.jpg");
      }
    }

    &__content {
      height: 276px;

      display: flex;
      align-items: center;
    }

    &__title {
      max-width: 595px;
    }
  }

  &-services {
    &__content {
      margin: 61px 0 0 0;
    }

    &__tips {
      margin: 71px 0 0 0;
    }

    &__footer {
      margin: 49px 0 0 0;
    }
  }

  &-difference {
    &__content {
      margin: 67px 0 0 0;
    }

    &__benefits {
      margin: 106px 0 0 0;
    }

    &__wrapper {
      padding: 93px 0 114px 0;

      background: url("../img/map-difference.svg") top center/cover no-repeat;
    }

    &__img {
      position: relative;
      height: 570px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      &::before {
        content: '';
        position: absolute;
        left: -1px;
        top: 0;
        display: block;
        width: 50px;
        height: 100%;

        background: url("../img/corner-right-gray.svg") center center/cover no-repeat;
        transform: rotate(180deg) scaleY(-1);
      }
    }
  }

  &-benefits {
    &__content {
      margin: 48px 0 0 0;
    }

    &__wrapper {
      display: grid;
      grid-template-columns: auto 554px;
      gap: 29px;
    }

    &__footer {
      margin: 110px 0 0 0;

      grid-column-end: span 2;
    }
  }

  &-news {
    &__list {
      margin-top: 41px;
    }

    &__footer {
      margin: 72px 0 0 0;
    }
  }

  &-partners {
    &__content {
      margin: 75px 0 0 0;
    }

    &__list {
      margin: 45px 0 0 0;
    }

    &__footer {
      margin: 30px 0 0 0;
    }

    &__buttons {
      display: flex;
      align-items: center;

      &-item {
        & + & {
          margin-left: 22px;
        }
      }
    }

    position: relative;

    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: calc(50% - 375px);
      height: 800px;
      width: 750px;
      display: block;

      background: url("../img/partners-bg.png") right center/contain no-repeat;
    }
  }

  &-download {
    &__content {
      display: grid;
      grid-template-columns: 790px auto;
      gap: 70px;
    }

    &__title {
      max-width: 747px;
    }

    &__links {
      margin-top: 50px;
    }

    &__code {
      margin-top: 52px;
    }
  }

  &-commands {
    &__wrapper {
      padding: 93px 0 124px 0;
    }

    &__content {
      margin-top: 76px;
    }
  }

  &-delivery {
    &__wrapper {
      padding: 93px 0 113px 0;
    }
  }

  &-contact {
    &__wrapper {
      display: grid;
      grid-template-columns: 505px auto;
      gap: 80px;
    }

    &__form {
      margin-top: 40px;
    }
  }

  &-settings{
    &__title{
      max-width: 635px;
      width: 100%;
    }
    &__desc{
      max-width: 580px;
      width: 100%;
    }
    &__content{
      margin: 25px 0 0 0;
    }
  }

  &-useful{
    &__content{
      display: grid;
      grid-template-columns: 200px auto;
      gap: 46px;
    }
  }


  &__subtitle {
    margin-bottom: 23px;

    &-label {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 400;

      font-size: 16px;
      line-height: 16px;

      text-transform: uppercase;
      color: #A7A7A7;
    }
  }

  &__title {
    &-label {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 700;

      font-size: 28px;
      line-height: 32px;

      color: #FAFAFA;

      text-transform: capitalize;

      & + & {
        margin: 18px 0 0 0;
      }

      &-accent {
        color: #E63739;
      }

      &-normal {
        text-transform: unset;
      }

      &-small {
        font-size: 24px;
        line-height: 28px;
      }

      &-big {
        font-size: 36px;
        line-height: 40px;
      }

      &-biggest {
        font-size: 45px;
        line-height: 45px;
      }
    }

    &-medium {
      max-width: 920px;
    }

    &-big {
      max-width: 1105px;
    }
  }

  &__desc {
    margin-top: 37px;

    &-small {
      margin-top: 26px;
    }

    &-text {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-size: 16px;
      line-height: 24px;
      color: #E5E5E5;
    }

    p + p {
      margin: 30px 0 0 0;
    }

    ul + p {
      margin: 30px 0 0 0;
    }

    ul {
      padding-left: 20px;

      li {
        &::before {
          content: '';
          color: #E63739;

          margin-right: 10px;
        }
      }
    }
  }
}

.video {
  width: 100%;
  height: 477px;

  &__item {
    width: 100%;
    height: 100%;
  }
}

.advantages {
  &-grid {
    &__wrapper {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(0, 260px));
      grid-auto-rows: 181px;
      gap: 35px;
    }
  }

  &-item {
    height: 100%;
    width: 100%;


    transition: .5s transform linear;

    &:hover{
      transform: scale(1.05);
    }

    &__wrapper {
      background: rgba(48, 47, 47, 0.85);
      border-radius: 5px;

      width: 100%;
      height: 100%;

      display: flex;
      align-items: center;
      justify-content: flex-start;
      flex-direction: column;

      padding: 25px 0 0 0;
    }

    &__icon {
      width: 70px;
      height: 70px;
    }

    &__content {
      margin: 20px 0 0 0;
    }

    &__label {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 700;

      font-size: 18px;
      line-height: 21px;

      text-align: center;

      color: #fff;
    }
  }
}

.plan {
  &-slider {
    padding: 30px 0;

    &__item {
      width: 285px;
    }
  }

  &-item {
    width: 100%;

    &__grid {
      display: grid;
      grid-template-columns: auto 650px;
      gap: 37px;
    }

    &__img {
      width: 100%;
      height: 559px;
      display: flex;
      align-items: center;
      justify-content: center;

      background: url("../img/plan-card-bg.jpg") center center/contain no-repeat;
    }

    &__row {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;

      &-offer {
        margin: 26px 0 0 0%;
      }

      &-include {
        margin: 32px 0 0 0;
      }
    }

    &__wrapper {
      width: 100%;
      height: 100%;

      position: relative;
    }

    &__name {
      position: absolute;
      left: calc(50% - 89px);
      top: -20px;

      width: 178px;
      height: 41px;

      background: #121212;
      border-radius: 50px;

      display: flex;
      align-items: center;
      justify-content: center;

      &-label {
        font-family: 'Kumbh Sans', sans-serif;
        font-weight: 700;

        font-size: 16px;

        color: #fff;

        text-transform: capitalize;

        &-accent {
          color: #E63739;
        }
      }
    }

    &__header {
      height: 131px;

      background: #302F2F;
      border-radius: 5px 5px 0px 0px;

      display: flex;
      align-items: center;
      justify-content: center;

      &-block {
        & + & {
          border-left: 1px solid #fff;
        }
      }
    }

    &__info {
      padding: 0 20px;

      &-big {
        padding: 0 45px;
      }

      &-title {
        font-family: 'Kumbh Sans', sans-serif;
        font-weight: 700;
        font-size: 42px;
        line-height: 42px;

        color: #Fff;
      }

      &-label {
        font-size: 24px;
      }

      &-text {
        margin-top: 5px;

        font-family: 'PF BeauSans Pro', sans-serif;
        font-size: 14px;
        line-height: 14px;
        text-transform: uppercase;

        color: #fff;
      }
    }

    &__content {
      background: rgba(196, 196, 196, 0.4);
      border-radius: 0 0 5px 5px;

      padding: 38px 24px 35px 24px;

      &-big {
        padding: 40px 75px 40px 75px;
      }

      &-block {
        padding: 0 0 17px 0;

        & + & {
          border-top: 1px solid #444444;

          padding: 17px 0 0 0;
        }
      }
    }

    &__price {
      font-family: 'Kumbh Sans', sans-serif;
      font-weight: 700;
      text-align: center;

      color: #fff;

      &-sign {
        font-size: 24px;
      }

      &-label {
        font-size: 55px;
      }

      &-text {
        font-weight: 400;
        font-size: 16px;

        text-transform: uppercase;
      }
    }

    &__text {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 700;
      font-size: 18px;
      height: 18px;

      color: #FAFAFA;

      text-transform: uppercase;
    }

    &__advantage {
      margin: 25px auto 0 auto;
      width: max-content;

      &-full {
        width: 100%;
        margin: 0;
      }

      &-item {
        display: flex;
        align-items: center;

        & + & {
          margin: 6px 0 0 0;
        }
      }

      &-icon {
        width: 16px;
        height: 16px;

        margin-right: 13px;
      }

      &-text {
        font-family: 'Proxima Nova', sans-serif;
        font-size: 16px;
        color: #fff;

        &-right {
          margin-left: auto;
        }
      }
    }

    &__footer {
      margin: 31px 0 0 0;

      &-small {
        margin: 25px 0 0 0;
      }

      &-text {
        margin: 25px 0 0 0;
        font-family: 'PF BeauSans Pro', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        color: #A7A7A7;
      }
    }

    &__button {
      box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
    }

    &__description {
      margin: 100px 0 0 0;
    }
  }

  &-checkbox {
    display: flex;
    align-items: center;

    &:hover {
      cursor: pointer;
    }

    &__item {
      display: none;

      &:checked {
        & ~ .plan-checkbox__checkmark {
          &::before {
            opacity: 1;
          }
        }
      }
    }

    &__checkmark {
      background: #302F2F;
      border: 1px solid #444444;
      border-radius: 2px;

      display: block;
      width: 14px;
      height: 14px;

      flex-shrink: 0;

      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        justify-content: center;
        color: #fff;

        font-size: 10px;

        opacity: 0;

        transition: .5s opacity;
      }
    }

    &__label {
      margin-left: 13px;

      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 400;
      font-size: 16px;
      height: 14px;

      color: #E5E5E5;
    }
  }

  &-description {
    &__grid {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
      gap: 36px;

      &-item {
        height: 270px;

        &-full {
          height: 390px;
          grid-column-end: span 3;
        }
      }
    }

    &__block {
      height: 100%;

      &-wrapper {
        height: 100%;
        padding: 57px 57px 0 57px;
        border: 1px solid #444444;
        border-radius: 5px;
      }

      &-title {
        font-family: 'PF BeauSans Pro', sans-serif;
        font-weight: 700;
        font-size: 22px;
        line-height: 24px;

        text-transform: capitalize;

        color: #FAFAFA;
      }

      &-content {
        margin-top: 20px;
      }

      &-text {
        font-family: 'PF BeauSans Pro', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;

        color: #E5E5E5;
      }
    }

    &__list {
      &-item {
        display: flex;
        align-items: flex-start;

        &::before {
          content: '';
          transform: translateY(5px);
          width: 10px;
          height: 10px;

          flex-shrink: 0;

          display: inline-block;

          background: #E5E5E5;
          border-radius: 50%;

          margin-right: 14px;
        }

        & + & {
          margin: 30px 0 0 0;
        }
      }
    }
  }

  &-calculate {
    &__wrapper {
      display: grid;
      grid-template-columns: repeat(2, minmax(0, 600px));
      gap: 60px;
    }
  }

  &-country {
    &__block {
      padding: 57px 57px 48px 57px;
      border: 1px solid #444444;
      border-radius: 5px;
    }
  }

  &__text {
    font-family: 'Kumbh Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    color: #E5E5E5;

    p + p {
      margin-top: 25px;
    }
  }

  &-tabs {
    overflow: hidden;

    &__content {
      margin-top: 46px;
    }

    &__nav {
      max-width: 1250px;
      width: 100%;

      border: 1px solid #444444;
      border-radius: 5px;

      &-item {
        position: relative;
        width: 25%;

        &.active {
          background: #E63739;
        }

        & + & {
          border-left: 1px solid #444444;
        }
      }

      &-link {
        width: 100%;
        height: 80px;

        display: flex;
        align-items: center;
        justify-content: center;

        font-family: 'PF BeauSans Pro', sans-serif;
        font-style: normal;
        font-weight: 700;
        font-size: 18px;
        line-height: 21px;

        text-align: center;

        color: #FFFFFF;
      }
    }
  }

  &-tabs {
  }
}

.banner {
  &-offer {
    background: url("../img/banner.jpg") center center/cover no-repeat;
  }

  &-second {
    background: url("../img/banner-second.jpg") center center/cover no-repeat;
  }

  &__content {
    height: 384px;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    font-family: 'Kumbh Sans', sans-serif;
    font-weight: 700;

    font-size: 55px;
    line-height: 55px;

    text-transform: capitalize;

    color: #fff;

    &-small {
      max-width: 492px;
    }

    &-accent {
      color: #E63739
    }
  }

  &__desc {
    margin-top: 29px;

    font-family: 'Kumbh Sans', sans-serif;
    font-weight: 500;
    font-size: 22px;
    color: #FFFFFF;
  }
}

.offer {
  &-block {
    width: 273px;
    height: 267px;

    background-color: #E63739;
    border-radius: 206px;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &__title {
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;

      text-align: center;
      color: #fff;
    }

    &__price {
      font-family: 'Kumbh Sans', sans-serif;
      font-weight: 700;

      font-size: 55px;
      color: #fff;

      margin: 5px 0 0 0;

      &-sign {
        font-size: 24px;
      }
    }
  }
}

.buy {
  &-block {
    height: 100%;

    &__wrapper {
      padding: 38px 28px 39px 43px;

      background: #302F2F;
      border-radius: 5px;
      height: 100%;

      display: flex;
      flex-direction: column;
    }

    &__content {
      display: flex;
    }

    &__icon {
      width: 60px;
      height: 60px;

      flex-shrink: 0;

      position: relative;

      svg {
        position: relative;
        z-index: 3;
      }

      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        left: calc(50% - 3px);
        top: 59px;
        width: 6px;
        height: 276px;
        display: block;

        background: linear-gradient(180deg, rgba(196, 196, 196, 0.5) 0%, rgba(196, 196, 196, 0) 100%);
      }
    }

    &__box {
      margin-left: 20px;
    }

    &__title {
      height: 60px;
      display: flex;
      align-items: center;

      font-size: 28px;
      line-height: 32px;

      text-transform: capitalize;

      color: #fff;

      font-weight: 700;
    }

    &__desc {
      margin-top: 15px;

      &-block {
        & + & {
          margin: 30px 0 0 0;
        }
      }
    }

    &__text {
      font-family: 'Kumbh Sans', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;

      color: #fff;

      &-bold {
        font-weight: 700;
      }
    }

    &__footer {
      margin-top: auto;
