  font-weight: 700;
  font-size: 16px;
  color: #fff;
  background: #E63739;
  border-radius: 5px;
  box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
  transition: all .3s linear;
}

.button-accent:hover {
  box-shadow: 0px 0px 10px #310f0f inset;
  background: rgba(230, 55, 57, 0.7);
}

.button-grey {
  font-family: 'PF BeauSans Pro', sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #fff;
  background: #302F2F;
  box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
  border-radius: 5px;
}

.button-white {
  font-family: 'PF BeauSans Pro',sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #FAFAFA;
  border: 1px solid #FAFAFA;
  border-radius: 5px;
}

.button-medium {
  max-width: 300px;
}

.button-small {
  max-width: 275px;
}

.button-big {
  max-width: 400px;
}

.list-accent {
  font-family: 'PF BeauSans Pro', sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #E5E5E5;
}

.list-accent li::before {
  content: '\2022';
  color: #E63739;
  display: inline-block;
  margin: 0 10px 0 0;
}

.tabs-content__item {
  display: none;
}

.tabs-content__item.active {
  display: block;
}

.checkbox {
  display: flex;
  align-items: center;
}

.checkbox:hover {
  cursor: pointer;
}

.checkbox__item {
  display: none;
}

.checkbox__item:checked ~ .checkbox__checkmark::before {
  opacity: 1;
}

.checkbox__checkmark {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border: 1px solid #A7A7A7;
  border-radius: 2px;
  position: relative;
  flex-shrink: 0;
}

.checkbox__checkmark::before {
  content: '';
  position: absolute;
  right: 0;
  top: calc(50% - 4.5px);
  width: 12px;
  height: 9px;
  display: block;
  background: url("../img/check.svg") center center/contain no-repeat;
  opacity: 0;
  transition: .3s opacity;
}

.checkbox__label {
  margin-left: 10px;
  line-height: 16px;
}

.checkbox__label a {
  text-decoration: underline;
  color: #E63739;
}

*[data-animate] {
  opacity: 0;
  transition: .5s opacity linear, .75s all linear;
}

*[data-animate].animated {
  opacity: 1;
}

*[data-animate='fade-right'] {
  transform: translateX(-100%);
}

*[data-animate='fade-right'].animated {
  transform: translateX(0);
}

*[data-animate='fade-left'] {
  transform: translateX(100%);
}

*[data-animate='fade-left'].animated {
  transform: translateX(0);
}

*[data-animate='fade-top'] {
  transform: translateY(100%);
}

*[data-animate='fade-top'].animated {
  transform: translateY(0);
}

*[data-animate='fade-bottom'] {
  transform: translateY(-100%);
}

*[data-animate='fade-bottom'].animated {
  transform: translateY(0);
}

