.nav-list__sub + .nav-item__link {
  display: flex;
  align-items: center;
}

.nav-list__sub + .nav-item__link::after {
  content: '';
  width: 12px;
  height: 12px;
  display: block;
  flex-shrink: 0;
  margin-left: 14px;
  background: url("../img/select-arrow.svg") center center/contain no-repeat;
}

.nav-list__item {
  position: relative;
}

.nav-list__item:hover::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 30px;
  bottom: -30px;
  left: 0;
  flex-shrink: 0;
  background: transparent;
}

.nav-list__item:hover .nav-list__sub {
  display: block;
}

.nav-list__item + .nav-list__item {
  margin-left: 92px;
}

.nav-list__sub {
  position: absolute;
  left: 0;
  top: calc(100% + 30px);
  background: #0B0B0B;
  border: 1px solid #302F2F;
  box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 10px 10px;
  padding: 2px 15px 4px 15px;
  width: 200px;
  display: none;
}

.nav-list__sub-item + .nav-list__sub-item {
  border-top: 1px solid #302F2F;
}

.nav-list__sub-link {
  display: block;
  padding: 12px 0 8px 0;
  font-family: 'Kumbh Sans', sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #FFFFFF;
}

.nav-item__link {
  font-family: 'PF BeauSans Pro', sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #e5e5e5;
  position: relative;
}

.nav-item__link:hover .nav-item__link-text::before {
  width: 100%;
}

.nav-item__link-text {
  position: relative;
}

.nav-item__link-text::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 0;
  height: 1px;
  display: block;
  background-color: #fff;
  transition: .3s width linear;
}

.nav-item__link-bold {
  font-weight: 700;
}

.burger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 32px;
  height: 24px;
}

.burger__item {
  width: 100%;
  height: 2px;
  background: #E63739;
  border-radius: 36px;
}

.burger__item:nth-child(1), .burger__item:nth-child(3) {
