}

@media screen and (max-width: 1280px) {
  .nav-list__item + .nav-list__item {
    margin-left: 50px;
  }
}

@media screen and (max-width: 991px) {
  .header__wrapper {
    padding: 12px 0;
  }
  .header__wrapper::before {
    display: none;
  }
  .header-wrapper__block-settings {
    margin-left: auto;
  }
  .header-settings__block + .header-settings__block {
    margin-left: 55px;
  }
  .header-settings__block-profile {
    display: none;
  }
  .header__burger {
    display: block;
  }
  .header__row-nav {
    position: fixed;
    display: none;
  }
}

@media screen and (max-width: 600px) {
