}

.section-hero__footer {
  position: absolute;
  z-index: 5;
  left: 0;
  bottom: 40px;
  width: 100%;
}

.hero-slider__item {
  position: relative;
}

.hero-slider__item-wrapper {
  position: relative;
  height: calc(100vh - 152px);
  min-height: 610px;
}

.hero-slider__item-img {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  width: 100%;
