.section{
  &-hero{
    &__wrapper{
      position: relative;

      &::after{
        content: '';

        position: absolute;
        left: 0;
        bottom: 0;
        z-index: 4;

        width: 100%;
        height: 137px;

        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 3.65%, rgba(0, 0, 0, 0.3) 100%);
      }
    }
    &__footer{
      position: absolute;
      z-index: 5;
      left: 0;
      bottom: 40px;

      width: 100%;
    }
  }
}

.hero{
  &-slider{
    &__item{
      position: relative;
      &-wrapper{
        position: relative;
        height:calc(100vh - 152px);
        min-height: 610px;
      }
      &-img{
        position: absolute;
        left: 0;
        top: 0;

        z-index: 2;

        width: 100%;
        height: 100%;
        &-mobile{
          display: none;
        }
      }
      &-content{
        position: relative;
        z-index: 3;

        display: flex;
        align-items: center;

        height: 100%;
      }
      &-title{
        font-family: 'PF BeauSans Pro', sans-serif;
        font-weight: 700;

        font-size: 45px;
        line-height: 45px;
        color: #FAFAFA;

        max-width: 640px;

        text-transform: capitalize;
        &-small{
          max-width: 570px;
        }
      }
      &-desc{
        margin-top: 25px;

        font-family: 'PF BeauSans Pro', sans-serif;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        color: #FFFFFF;

        max-width: 482px;
      }
      &-button{
        margin-top: 43px;

        width: 237px;
      }
    }
    &__pagination{
      .swiper{
        &-pagination{
          &-bullet{
            width: 50px;
            height: 5px;

            background: rgba(250, 250, 250, 0.5);
            border-radius: 10px;

            opacity: 1;
            &-active{
              background: #FAFAFA;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .hero{
    &-slider{
      &__item{
        &-wrapper{
          min-height: auto;
          height: 540px;
        }
        &-content{
          align-items: flex-start;
          padding: 55px 0;
        }
        &-title{
          font-size: 30px;
          line-height: 36px;
        }
        &-desc{
          margin-top: 15px;
        }
        &-img{
          &-mobile{
            display: block;
          }
          &-desktop{
            display: none;
          }
        }
      }
      &__pagination{
        .swiper-pagination-bullet{
          width: 30px;
          height: 3px;
        }
      }
    }
  }
}