.header{
  border-bottom:1px solid #302F2F;
  &-wrapper{
    &__content{
      display: flex;
      justify-content: space-between;
    }
    &__block{
      &-settings{
        margin-left: 120px;

        width: 100%;
      }
    }
  }
  &-settings{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__block{
      & + &{
        margin-left: 62px;
      }
    }
  }
  &-nav{
    &__list{
      display: flex;
      align-items: center;
    }
  }
  &__wrapper{
    background-color: #0B0B0B;
    position: relative;
    &::before{
      content: '';
      position: absolute;
      z-index: 0;
      right: 0;
      top: 0;
      width: 1080px;
      height: 76px;
      background: #000000;
      border-radius: 0px 0px 0px 10px;
    }
  }
  &__burger{
    display: none;
  }
  &__logo{
    display: flex;
    align-items: center;
    justify-content: center;

    height: 100%;

    position: relative;
    z-index: 3;
  }
  &__row{
    position: relative;
    z-index: 3;
    &-settings{
      display: flex;
      align-items: center;
      justify-content: flex-end;

      height: 76px;
    }
    &-nav{
      height: 76px;

      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.logo{
  &__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__header{
    width: 145px;
    height: 80px;

    flex-shrink: 0;
  }
  &__img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  &__text{
    font-family: 'Exo', sans-serif;
    font-weight: 900;
    font-size: 10px;

    letter-spacing: 0.05em;
    text-transform: uppercase;

    color: #FFFFFF;
  }
}

.profile{
  &-link{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__icon{
      width: 22px;
      height: 22px;

      flex-shrink: 0;
    }
    &__content{
      margin-left: 10px;
    }
    &__text{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-size: 14px;
      font-weight: 400;

      color:#fff;
    }
  }
}

.nav-list__sub + .nav-item__link{
  display: flex;
  align-items: center;
  &::after{
    content: '';
    width: 12px;
    height: 12px;
    display: block;

    flex-shrink: 0;

    margin-left: 14px;
    background: url("../img/select-arrow.svg") center center/contain no-repeat;
  }
}

.nav{
  &-list{
    &__item{
      position: relative;
      &:hover{
        &::before{
          content: '';
          position: absolute;
          width: 100%;
          height: 30px;
          bottom: -30px;
          left: 0;

          flex-shrink: 0;

          background: transparent;
        }
        .nav-list{
          &__sub{
            display: block;
          }
        }
      }
      & + &{
        margin-left: 92px;
      }
    }
    &__sub{
      position: absolute;
      left: 0;
      top: calc(100% + 30px);

      background: #0B0B0B;
      border: 1px solid #302F2F;
      box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.25);
      border-radius: 0px 0px 10px 10px;

      padding: 2px 15px 4px 15px;
      width: 200px;

      display: none;
      &-item{
        & + &{
          border-top: 1px solid #302F2F;
        }
      }
      &-link{
        display: block;
        padding: 12px 0 8px 0;

        font-family: 'Kumbh Sans', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;

        color: #FFFFFF;

      }
    }
  }
  &-item{
    &__link{
      font-family: 'PF BeauSans Pro', sans-serif;
      font-weight: 300;

      font-size: 18px;
      color:#e5e5e5;
      position: relative;
      &:hover{
        .nav-item__link-text{
          &::before{
            width: 100%;
          }
        }
      }
      &-text{
        position: relative;
        &::before{
          content: '';
          position: absolute;
          left: 0;
          bottom: -1px;
          width: 0;
          height: 1px;
          display: block;

          background-color: #fff;

          transition: .3s width linear;
        }
      }
      &-bold{
        font-weight: 700;
      }
    }
  }
}

.burger{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 32px;
  height: 24px;
  &__item{
    width: 100%;
    height: 2px;
    background: #E63739;
    border-radius: 36px;
    &:nth-child(1),&:nth-child(3){
      width: 26px;
    }
  }
}

@media screen and (max-width: 1280px) {
  .nav{
    &-list{
      &__item{
        & + &{
          margin-left: 50px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header{
    &__wrapper{
      padding: 12px 0;
      &::before{
        display: none;
      }
    }
    &-wrapper{
      &__block{
        &-settings{
          margin-left: auto;
        }
      }
    }
    &-settings{
      &__block{
        & + &{
          margin-left: 55px;
        }
        &-profile{
          display: none;
        }
      }
    }
    &__burger{
      display: block;
    }
    &__row{
      &-nav{
        position: fixed;

        display: none;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .header{
    &-settings{
      &__block{
        & + &{
          margin-left: 40px
        }
      }
    }
  }
  .logo{
    &__header{
      width: 77px;
      height: 42px;
    }
    &__text{
      font-size: 5px;
    }
  }
}