.container{
  max-width: 1298px;
  width: 100%;

  margin: 0 auto;

  padding: 0 20px;
}

.icon{
  width: 100%;
  height: 100%;
}

.img{
  &-full{
    width: 100%;
    height: 100%;
  }
}

.button{
  -webkit-appearance: none;

  outline: none;
  border: none;

  width: 100%;
  height: 46px;
  line-height: 46px;


  display: flex;
  align-items: center;
  justify-content: center;
  &:hover{
    cursor: pointer;
  }
  &-accent{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-weight: 700;
    font-size: 16px;

    color:#fff;
    background: #E63739;
    border-radius: 5px;

    box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);

    transition: all .3s linear;
    &:hover{
      box-shadow: 0px 0px 10px rgba(49, 15, 15, 1) inset;

      background: rgba(#E63739,.7);
    }
  }
  &-grey{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-weight: 700;
    font-size: 16px;

    color:#fff;
    background: #302F2F;
    box-shadow: 0px 4px 10px rgba(49, 15, 15, 0.25);
    border-radius: 5px;
  }
  &-white{
    font-family: 'PF BeauSans Pro',sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-align: center;

    color: #FAFAFA;

    border: 1px solid #FAFAFA;
    border-radius: 5px;
  }
  &-medium{
    max-width: 300px;
  }
  &-small{
    max-width: 275px;
  }
  &-big{
    max-width: 400px;
  }
}

.list{
  &-accent{
    font-family: 'PF BeauSans Pro', sans-serif;
    font-size: 16px;
    line-height: 24px;

    color:#E5E5E5;
    li{
      &::before{
        content: '\2022';
        color: #E63739;

        display: inline-block;

        margin: 0 10px 0 0 ;
      }
    }
  }
}

.tabs{
  &-content{
    &__item{
      display: none;
      &.active{
        display: block;
      }
    }
  }
}

.checkbox{
  display: flex;
  align-items: center;
  &:hover{
    cursor: pointer;
  }
  &__item{
    display: none;
    &:checked ~ .checkbox__checkmark{
      &::before{
        opacity: 1;
      }
    }
  }
  &__checkmark{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 16px;
    height: 16px;

    border: 1px solid #A7A7A7;
    border-radius: 2px;

    position: relative;

    flex-shrink: 0;
    &::before{
      content: '';
      position: absolute;
      right: 0;
      top: calc(50% - 4.5px);
      width: 12px;
      height: 9px;
      display: block;

      background: url("../img/check.svg") center center/contain no-repeat;
      opacity: 0;

      transition: .3s opacity;
    }
  }
  &__label{
    margin-left: 10px;
    line-height: 16px;

    a{
      text-decoration: underline;
      color:#E63739;
    }
  }
}


*[data-animate]{
  opacity: 0;

  transition: .5s opacity linear, .75s all linear;

  &.animated{
    opacity: 1;
  }
}

*[data-animate='fade-right']{
  transform: translateX(-100%);

  &.animated{
    transform: translateX(0);
  }
}

*[data-animate='fade-left']{
  transform: translateX(100%);

  &.animated{
    transform: translateX(0);
  }
}


*[data-animate='fade-top']{
  transform: translateY(100%);

  &.animated{
    transform: translateY(0);
  }
}


*[data-animate='fade-bottom']{
  transform: translateY(-100%);

  &.animated{
    transform: translateY(0);
  }
}